.graph-modal {
  --transition-time: 0.3s;
  position: fixed;
  inset: 0;
  z-index: 1000;
  overflow: hidden auto;
  text-align: center;
  background-color: var(--rgba-dark-800-50);
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
  cursor: pointer;
}

.graph-modal::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.graph-modal__container {
  border-radius: 30px;
  padding: 70px 54px;
  background-color: var(--color-orange-200);
  position: relative;
  display: inline-block;
  display: none;
  vertical-align: middle;
  margin: 50px auto;
  width: 100%;
  max-width: 410px;
  text-align: left;
  cursor: default;
}

.graph-modal__close {
  border-radius: 50%;
  width: 42px;
  height: 42px;
  background-color: var(--color-dark-600);
  outline: 0;
  transition: box-shadow 0.3s, background-color 0.3s;
  position: absolute;
  right: 20px;
  top: 20px;
}

.graph-modal__close:focus-visible {
  box-shadow: var(--focus-shadow);
  background-color: transparent;
}

.graph-modal__close::before {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 16px;
  height: 16px;
  background-image: url("../img/icons/close.svg");
  transform: translate(-50%, -50%);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
  transition: filter 0.3s;
}

.graph-modal__close:focus-visible::before {
  filter: invert(91%) sepia(1%) saturate(996%) hue-rotate(339deg) brightness(101%) contrast(83%);
}

.graph-modal__title {
  display: block;
  font-family: var(--font-secondary);
  font-weight: 600;
  line-height: 1.1;
  margin-bottom: 18px;
  font-size: 38px;
  letter-spacing: -0.03em;
}

.graph-modal__text {
  font-weight: 600;
  font-size: 16px;
}

.graph-modal__text p:not(:last-child) {
  margin-bottom: 8px;
}

.graph-modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.graph-modal__container.graph-modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
}

.fade,
.fade.animate-open {
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
}

.fadeInUp,
.fadeInUp.animate-open {
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
}

