body {
  font-family: var(--font-main), "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.3;
  color: var(--color-dark-800);
  background-color: var(--color-orange-100);
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
  max-width: 1336px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100vh;
}

.wrapper--without-overflow {
  overflow: unset;
}

.main {
  flex: 1 1 auto;
}

.lock {
  overflow: hidden;
}

.sr-only {
  position: absolute;
  clip-path: inset(100%);
  overflow: hidden;
  margin: 1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip: rect(0 0 0 0);
}

.disable-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.swiper-slide {
  box-sizing: border-box;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: var(--color-black);
  opacity: 0;
}

.overlay--active {
  z-index: 4;
  opacity: 0.5;
}

.large,
.title,
.subtitle {
  display: block;
  font-family: var(--font-secondary);
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: -0.03em;
}

.large {
  margin-bottom: 20px;
  font-size: 50px;
}

.title {
  margin-bottom: 40px;
  font-size: 38px;
  text-align: center;
}

.subtitle {
  margin-bottom: 24px;
  font-size: 26px;
}

.caption {
  font-weight: 600;
  font-size: 16px;
  line-height: 1.1;
}

.logo {
  display: inline-block;
  width: 100%;
  height: 42px;
  max-width: 190px;
}

.logo--cpi {
  height: 54px;
  max-width: 248px;
}

.logo--mp {
  height: 80px;
  max-width: 208px;
}

.logo__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0;
  outline: 0;
  transition: box-shadow 0.3s, background-color 0.3s;
  border-radius: 15px;
  padding: 16px 32px;
  font-size: 16px;
  line-height: 1.1;
  text-align: center;
  color: var(--color-white);
  background-color: var(--color-dark-600);
  transition: background-color 0.3s, box-shadow 0.3s, color 0.3s;
}

.button:focus-visible {
  box-shadow: var(--focus-shadow);
  background-color: transparent;
}

.button:focus-visible {
  color: var(--color-dark-600);
}

.button:disabled {
  background-color: var(--color-orange-150);
}

.swiper-controls {
  display: flex;
  flex-direction: row;
  gap: 6px;
  align-items: center;
}

.swiper-controls__button {
  outline: 0;
  transition: box-shadow 0.3s, background-color 0.3s;
  border-radius: 50%;
  width: 42px;
  height: 42px;
  background-color: var(--color-dark-600);
  position: relative;
}

.swiper-controls__button:focus-visible {
  box-shadow: var(--focus-shadow);
  background-color: transparent;
}

.swiper-controls__button::after {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10px;
  height: 10px;
  background-image: url("../img/icons/arrow.svg");
  transform: translate(-50%, -50%);
  content: "";
  transition: filter 0.3s;
}

.swiper-controls__button--next {
  transform: rotate(180deg);
}

.swiper-controls__button--disabled {
  background-color: var(--color-orange-150);
  pointer-events: none;
}

.swiper-controls__button:focus-visible::after {
  filter: invert(91%) sepia(1%) saturate(996%) hue-rotate(339deg) brightness(101%) contrast(83%);
}

.choices[data-type*=select-one]::after {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  right: 20px;
  margin-top: 0;
  border: 0;
  width: 7px;
  height: 12px;
  background-image: url("../img/icons/arrow.svg");
  filter: invert(92%) sepia(67%) saturate(503%) hue-rotate(22deg) brightness(100%) contrast(104%);
  transform: rotate(-90deg) translateX(50%);
  transition: transform 0.3s;
}

.choices[data-type*=select-one].is-open::after {
  margin-top: 0;
  transform: rotate(90deg) translateX(-50%);
}

.choices.is-open .choices__inner {
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}

.choices__inner {
  display: flex;
  align-items: center;
  border: 0;
  border-radius: 30px;
  padding: 9px 20px;
  background-color: var(--color-orange-100);
}

.choices__item {
  font-weight: 600;
  font-size: 16px;
}

.choices__list {
  background-color: var(--color-orange-100);
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
  margin-top: 0;
  border: 0;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  padding: 5px 24px 24px;
  background-color: var(--color-orange-100);
}

.choices__list--dropdown .choices__item,
.choices__list[aria-expanded] .choices__item {
  padding: 10px 0;
  font-size: 16px;
}

.choices__list--dropdown .choices__item:last-child,
.choices__list[aria-expanded] .choices__item:last-child {
  padding-bottom: 0;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  color: var(--color-orange-400);
  background-color: transparent;
}

.choices__list--single {
  padding: 0;
}

.header {
  position: relative;
  z-index: 50;
  height: 92px;
  font-weight: 600;
  background-color: var(--color-white);
}

.header--fixed .header__container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: inherit;
}

.header__container {
  top: -50px;
  height: inherit;
  background-color: inherit;
  transition: top 0.3s;
}

.header__nav {
  display: flex;
  flex-direction: row;
  gap: 30px;
  align-items: center;
  padding: 25px 0;
  background-color: var(--color-white);
}

.header__logo {
  position: relative;
  z-index: 50;
  margin-right: auto;
}

.burger-button {
  --burger-width: 30px;
  --burger-height: 24px;
  --burger-line-height: 1px;
  --burger-border-radius: 0;
  --burger-color: var(--color-black);
  position: relative;
  width: var(--burger-width);
  height: var(--burger-height);
  color: var(--burger-color);
}

.burger-button::before,
.burger-button::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentcolor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger-button::before {
  top: 0;
}

.burger-button::after {
  top: calc(100% - var(--burger-line-height));
}

.burger-button__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentcolor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}

.burger-button--active::before,
.burger-button--active::after {
  top: 50%;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger-button--active::before {
  transform: rotate(45deg);
}

.burger-button--active::after {
